import produce from 'immer';
import { groupAndFlatten } from 'cb-utils/groupOptions';

import {
  createIsVisibleSetting,
  createBackgroundColorSetting,
  createFontFamilySetting,
  createFontColorSetting,
  createActiveTabUnderlineColorSetting,
} from '../Portal/pluginConstants';

import PortalModel from 'cb-utils/models/PortalModel';
const portalModel = PortalModel.getInstance();

const WIDGETS_SUB_MENU = 'widgets';

export const themeConfig = {
  menus: {
    name: 'main',
    display_name: 'Main',
    subMenus: [
      {
        name: 'portal',
        display_name: 'Portal',
        subMenus: [
          {
            name: 'styles',
            display_name: 'Styles',
            fields: [createBackgroundColorSetting()],
          },
        ],
      },
      {
        name: 'header',
        display_name: 'Header',
        subMenus: [
          {
            name: 'styles',
            display_name: 'Styles',
            fields: [createBackgroundColorSetting()],
          },
          {
            name: 'burgerBars',
            display_name: 'BurgerBars',
            fields: [createFontColorSetting(), createIsVisibleSetting()],
          },
          {
            name: 'dropdown',
            display_name: 'Dropdown',
            fields: [createFontColorSetting(), createFontFamilySetting()],
          },
        ],
      },
      {
        name: 'panes',
        display_name: 'Panes',
        subMenus: [
          {
            name: 'primary',
            display_name: 'Primary',
            subMenus: [
              {
                name: 'body',
                display_name: 'Body',
                fields: [createBackgroundColorSetting()],
              },
              {
                name: 'header',
                display_name: 'Header',
                fields: [
                  createBackgroundColorSetting(),
                  createFontFamilySetting(),
                  createFontColorSetting(),
                ],
              },
              {
                name: 'tabs',
                display_name: 'Tabs',
                fields: [
                  createBackgroundColorSetting(),
                  createFontColorSetting(),
                  createActiveTabUnderlineColorSetting(),
                ],
              },
            ],
          },
          {
            name: 'secondary',
            display_name: 'Secondary',
            subMenus: [
              {
                name: 'body',
                display_name: 'Body',
                fields: [createBackgroundColorSetting()],
              },
              {
                name: 'header',
                display_name: 'Header',
                fields: [
                  createBackgroundColorSetting(),
                  createFontFamilySetting(),
                  createFontColorSetting(),
                ],
              },
              {
                name: 'tabs',
                display_name: 'Tabs',
                fields: [
                  createBackgroundColorSetting(),
                  createFontColorSetting(),
                  createActiveTabUnderlineColorSetting(),
                ],
              },
            ],
          },
          {
            name: 'flyout',
            display_name: 'Flyout',
            subMenus: [
              {
                name: 'body',
                display_name: 'Body',
                fields: [createBackgroundColorSetting()],
              },
              {
                name: 'header',
                display_name: 'Header',
                fields: [createBackgroundColorSetting()],
              },
            ],
          },
        ],
      },
      {
        name: WIDGETS_SUB_MENU,
        display_name: 'Widgets',
        subMenus: [], // these are added at run time when CB_PORTAL.registerWidget is called
      },
    ],
  },
};

const getThemeConfig = () => {
  const config = {
    ...themeConfig,
  };
  const themeSettings = portalModel.getThemeableSettingsForWidgets();
  // todo: save the result to a variable so we aren't constantly computing this
  return produce(config, (draftState) => {
    draftState.menus.subMenus.map((sub) => {
      if (sub.name === WIDGETS_SUB_MENU) {
        const settings = groupAndFlatten(themeSettings);
        sub.subMenus = [...settings];
      }
    });
  });
};

export default getThemeConfig;



