/**
 *
 * PortalThemeList
 *
 */

import React from 'react';
import List from '@material-ui/core/List';
import Radio from '@material-ui/core/Radio';
import { PortalTheme } from 'containers/Portal/themes/types';
import { DevConsoleChildListItem } from 'containers/PortalDevConsoleSections/utils';
// import styled from 'styled-components';

interface PortalThemeListProps {
  themes: PortalTheme[];
  currentTheme: PortalTheme;
  onChange: (idx: number) => void;
}

class PortalThemeList extends React.PureComponent<PortalThemeListProps, {}> {
  render() {
    const { currentTheme, themes } = this.props;
    return (
      <List>
        {themes.map((theme, idx) => {
          const onChange = () => this.props.onChange(idx);
          const active = theme.info.name === currentTheme.info.name;
          return (
            <DevConsoleChildListItem
              data-cy="portal-theme-list-item"
              key={idx}
              content={theme.info.display_name}
              action={<Radio onChange={onChange} checked={active} />}
              forceShowAction
              active={active}
              onClick={onChange}
            />
          );
        })}
      </List>
    );
  }
}

export default PortalThemeList;



