import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the cbThemeEditorContainer state domain
 */
const selectCbThemeEditorContainerDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by CbThemeEditorContainer
 */

const makeSelectCbThemeEditorContainer = () =>
  createSelector(selectCbThemeEditorContainerDomain(), (substate) => substate);

const selectIsLoaded = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.isLoaded,
  );

const selectSelectedSubMenus = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.selectedSubMenus,
  );

const selectShowAdvanced = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.showAdvanced,
  );

const selectCurrentMenuPath = () =>
  createSelector(selectCbThemeEditorContainerDomain(), (substate) =>
    substate.selectedSubMenus.map((menu) => menu.name),
  );

const selectIsPaletteEditorOpen = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.isPaletteEditorOpen,
  );

const selectIsGraphColorEditorOpen = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.isGraphColorEditorOpen,
  );

const selectIsDropdownOpen = () =>
  createSelector(
    selectCbThemeEditorContainerDomain(),
    (substate) => substate.isDropdownOpen,
  );

export default makeSelectCbThemeEditorContainer;
export {
  selectCbThemeEditorContainerDomain,
  selectSelectedSubMenus,
  selectShowAdvanced,
  selectIsLoaded,
  selectCurrentMenuPath,
  selectIsPaletteEditorOpen,
  selectIsGraphColorEditorOpen,
  selectIsDropdownOpen,
};



