/*
 *
 * CbThemeEditorContainer reducer
 *
 */

import produce from 'immer';
import { ActionConstants } from './actionConstants';
import { ActionTypes } from './actions';
import { TypedSettingDefinition } from 'utils/types';

interface ThemeMenuBase {
  name: string;
  display_name: string;
  disabled?: boolean;
}
export interface ThemeMenuWithSubMenus extends ThemeMenuBase {
  subMenus: ThemeMenuOptions[];
}
export interface ThemeMenuWithFields extends ThemeMenuBase {
  fields: TypedSettingDefinition[];
}
export type ThemeMenuOptions = ThemeMenuWithSubMenus | ThemeMenuWithFields;

export interface State {
  isLoaded: boolean;
  selectedSubMenus: ThemeMenuOptions[];
  showAdvanced: boolean;
  isGraphColorEditorOpen: boolean;
  isPaletteEditorOpen: boolean;
  isDropdownOpen: boolean;
}

export const initialState: State = {
  isLoaded: false,
  selectedSubMenus: [],
  showAdvanced: false,
  isGraphColorEditorOpen: false,
  isPaletteEditorOpen: false,
  isDropdownOpen: false,
};

const cbThemeEditorContainerReducer = produce(
  (state: State, action: ActionTypes) => {
    switch (action.type) {
      case ActionConstants.INIT_SELECTED_SUB_MENU:
        state.selectedSubMenus.push(action.payload.menu);
        state.isLoaded = true;
        return;
      case ActionConstants.GO_BACK_TO_MENU:
        state.selectedSubMenus.splice(action.payload.idx + 1);
        return;
      case ActionConstants.SELECT_SUB_MENU:
        state.selectedSubMenus[action.payload.idx + 1] = action.payload.menu;
        return;
      case ActionConstants.TOGGLE_ADVANCED:
        state.showAdvanced = !state.showAdvanced;
        return;
      case ActionConstants.TOGGLE_ACCORDION:
        state[action.payload.field] = !state[action.payload.field];
        return;
      case ActionConstants.TOGGLE_DROPDOWN:
        state.isDropdownOpen = !state.isDropdownOpen;
        return;
      default:
        return state;
    }
  },
  initialState,
);

export default cbThemeEditorContainerReducer;



