/**
 *
 * CbStyleNavigator
 *
 */

import React from 'react';
import styled from 'styled-components';

import { Breadcrumb, BreadcrumbItem, Button } from 'reactstrap';

import AngleRight from 'react-icons/lib/fa/angle-right';

// @ts-ignore - incorrect type def
import { CSSTransitionGroup } from 'react-transition-group';

import CbPluginSettingEditor from '../CbPluginSettingEditor';
import {
  ThemeMenuWithSubMenus,
  ThemeMenuOptions,
  ThemeMenuWithFields,
} from 'containers/CbThemeEditorContainer/reducer';
import { TypedSettingDefinition } from 'utils/types';
import withStyledProps from 'cb-utils/withStyledProps';
import styleVars from 'cssOverrides/styleVars';

// import { FormattedMessage } from 'react-intl';
// import messages from './messages';

const AdvancedSection = styled.div`
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  .submenu-enter.left {
    transform: translate(-100%);
  }
  .submenu-enter.submenu-enter-active.left {
    transform: translate(0%);
    transition: transform 500ms ease-in-out;
  }
  .submenu-leave.left {
    transform: translate(-100%);
  }
  .submenu-leave.submenu-leave-active.left {
    transform: translate(0%);
    transition: transform 500ms ease-in-out;
  }

  .submenu-enter.right {
    transform: translate(0%);
  }
  .submenu-enter.submenu-enter-active.right {
    transform: translate(-100%);
    transition: transform 500ms ease-in-out;
  }
  .submenu-leave.right {
    transform: translate(0%);
  }
  .submenu-leave.submenu-leave-active.right {
    transform: translate(-100%);
    transition: transform 500ms ease-in-out;
  }
`;

const MenuWrapper = styled.div`
  display: flex;
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
`;
const myFontColor = styleVars.gray200;
const myDisabledFontColor = styleVars.gray400;

const Menu = withStyledProps<{ order: number }>()(styled.div)`
  width: 100%;
  color: ${myFontColor};
  order: ${(props) => props.order};
  .card {
    color: black;
    svg {
      color: black;
    }
  }
`;

const SubMenuButton = styled(Button)`
  &&&:disabled {
    ~ :not([disabled]) {
      padding-left: 1.5em;
    }
    color: ${myDisabledFontColor};
  }
  && {
    color: ${myFontColor};
  }
  display: flex !important;
  justify-content: flex-start;
`;

const SubMenuIconContainer = styled.span`
  margin-left: auto;
`;

const SubMenuDisplayName = withStyledProps<{ isDivider: boolean }>()(
  styled.span,
)`
  font-size: ${(props) => (props.isDivider ? '1.2em' : '1em')};
`;

interface IProps {
  selectedMenus: ThemeMenuOptions[];
  handleFieldChange: (
    name: string,
    e: React.ChangeEvent<HTMLInputElement>,
  ) => void;
  getFieldValue: (name: string) => void;
  goBackToMenu: (idx: number) => void;
  selectSubMenu: (menu: ThemeMenuOptions, idxToReplace: number) => void;
}

interface IState {
  animationDirection: 'right' | 'left';
}

class CbStyleNavigator extends React.Component<IProps, IState> {
  state: IState = {
    animationDirection: 'right',
  };

  render() {
    const menuIdx = this.props.selectedMenus.length - 1;
    return (
      <AdvancedSection>
        <Breadcrumb>
          {this.props.selectedMenus.map(this._renderBreadcrumb)}
        </Breadcrumb>
        <CSSTransitionGroup
          component={MenuWrapper}
          transitionName="submenu"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}
        >
          {this._renderMenu(this.props.selectedMenus[menuIdx], menuIdx)}
        </CSSTransitionGroup>
      </AdvancedSection>
    );
  }

  goBackToMenu(idx: number) {
    this.setState({
      ...this.state,
      animationDirection: 'left',
    });
    // note: we use setTimeout so that our animationDirection has time to set itself within the rendered menus
    // if we don't have this - our animationDirection will become stale resulting in incorrect transitions
    setTimeout(() => this.props.goBackToMenu(idx));
  }

  selectSubMenu(menu: ThemeMenuOptions, idxToReplace: number) {
    this.setState({
      ...this.state,
      animationDirection: 'right',
    });
    // note: we use setTimeout so that our animationDirection has time to set itself within the rendered menus
    // if we don't have this - our animationDirection will become stale resulting in incorrect transitions
    setTimeout(() => this.props.selectSubMenu(menu, idxToReplace));
  }

  _renderBreadcrumb = (
    menu: ThemeMenuWithSubMenus,
    idx: number,
    arr: ThemeMenuWithSubMenus[],
  ) => {
    const isActive = idx + 1 === arr.length;
    const crumbText = menu.display_name;
    let crumbContent;
    if (isActive) {
      crumbContent = crumbText;
    } else {
      // we supply a tabIndex in order to reset styling on anchor tags that don't have an href
      crumbContent = (
        <a tabIndex={idx} onClick={this.goBackToMenu.bind(this, idx)}>
          {crumbText}
        </a>
      );
    }

    return (
      <BreadcrumbItem key={idx} active={isActive}>
        {crumbContent}
      </BreadcrumbItem>
    );
  };

  _renderMenu = (menu: ThemeMenuOptions, idx: number) => {
    return (
      <Menu key={idx} className={this.state.animationDirection} order={idx}>
        {(menu as ThemeMenuWithSubMenus).subMenus &&
          (menu as ThemeMenuWithSubMenus).subMenus.map(
            (subMenu, subMenuIdx, arr) =>
              this._renderSubMenuLink(subMenu, subMenuIdx, arr, idx),
          )}
        {(menu as ThemeMenuWithFields).fields &&
          this._renderFields((menu as ThemeMenuWithFields).fields)}
      </Menu>
    );
  };

  _renderSubMenuLink = (
    subMenu: ThemeMenuOptions,
    subMenuIdx: number,
    arr: ThemeMenuOptions[],
    menuIdx: number,
  ) => {
    return (
      <SubMenuButton
        key={subMenu.name}
        block
        color="link"
        size="sm"
        data-cy={subMenu.name}
        disabled={subMenu.disabled}
        onClick={this.selectSubMenu.bind(this, subMenu, menuIdx)}
      >
        <SubMenuDisplayName isDivider={subMenu.disabled}>
          {subMenu.display_name}
        </SubMenuDisplayName>
        {!subMenu.disabled && (
          <SubMenuIconContainer>
            <AngleRight />
          </SubMenuIconContainer>
        )}
      </SubMenuButton>
    );
  };

  _renderFields = (fields: TypedSettingDefinition[]) => {
    if (fields.length > 0) {
      return fields.map(this._renderField);
    } else {
      return <div>No themeable fields</div>;
    }
  };

  _renderField = (field: TypedSettingDefinition, idx: number) => {
    return (
      <CbPluginSettingEditor
        key={idx}
        {...field}
        onChange={this.props.handleFieldChange.bind(this, field.name)}
        value={this.props.getFieldValue(field.name)}
        size={12}
      />
    );
  };
}

export default CbStyleNavigator;



