/**
 *
 * CbThemeEditor
 *
 */

import React from 'react';
import styled from 'styled-components';

import PaletteIcon from 'react-icons/lib/md/palette';
import ColorizeIcon from 'react-icons/lib/md/colorize';
import TuneIcon from 'react-icons/lib/md/tune';

import get from 'lodash/get';

import CbStyleNavigator from '../CbStyleNavigator';
import CbPluginSettingEditor from '../CbPluginSettingEditor';
import { PortalTheme, PortalMacroTheme } from 'containers/Portal/themes/types';
import { SettingTypes } from 'utils/types';
import getThemeConfig from 'containers/CbThemeEditorContainer/themeConfig';
import { ThemeMenuOptions } from 'containers/CbThemeEditorContainer/reducer';
import CBSelectOrButtons from '../CBSelectOrButtons';
import PortalThemeList from '../PortalThemeList';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
import styleVars from 'cssOverrides/styleVars';
import { DevConsoleChildListItem } from 'containers/PortalDevConsoleSections/utils';

const Wrapper = styled.div`
  height: 100%;
  padding: 1rem;
  min-height: 18rem;
`;

const PaletteWrapper = styled.div`
  && * {
    color: ${styleVars.gray200};
  }
  font-size: 0.8em;
`;

const PaletteIconWrapper = styled(PaletteIcon)`
  font-size: 1.5em;
  color: #9e9e9e;
`;

const ColorizeIconWrapper = styled(ColorizeIcon)`
  font-size: 1.5em;
  color: #9e9e9e;
`;

const TuneIconWrapper = styled(TuneIcon)`
  font-size: 1.5em;
  color: #9e9e9e;
`;

// import { FormattedMessage } from 'react-intl';
// import messages from './messages';

interface IProps {
  showAdvanced: boolean;
  themes: PortalTheme[];
  isPaletteEditorOpen: boolean;
  theme: PortalTheme;
  isGraphColorEditorOpen: boolean;
  config: ReturnType<typeof getThemeConfig>;
  handlePrimaryColorChange: (val: string) => void;
  handleSecondaryColorChange: (val: string) => void;
  handlePaletteTextChange: (field: string, val: string) => void;
  currentMenuPath: string[];
  handleFieldChange: (path: string[], field: string, val: string) => void;
  handleGraphColorChange: (field: string, val: string) => void;
  toggleAccordion: (field: string) => void;
  changeTheme: (idx: number) => void;
  toggleAdvanced: () => void;
  goBackToMenu: (idx: number) => void;
  selectSubMenu: (menu: ThemeMenuOptions, idx: number) => void;
  selectedSubMenus: ThemeMenuOptions[];
}

const macroThemeValue = 'macro';
const microThemeValue = 'micro';
const themeButtons = [
  { value: macroThemeValue, name: 'Macro' },
  { value: microThemeValue, name: 'Micro' },
];

class CbThemeEditor extends React.PureComponent<IProps, {}> {
  render() {
    return (
      <Wrapper>
        <CBSelectOrButtons
          options={themeButtons}
          value={this.getThemeLevel()}
          onChange={this._toggleAdvanced}
          activeColor="secondary"
          inactiveColor="outline-secondary"
        />
        <hr />
        {!this.props.showAdvanced && (
          <div>
            <DevConsoleChildListItem
              data-cy="select-base-theme"
              icon={<PaletteIconWrapper />}
              content={<FormattedMessage {...messages.selectBaseTheme} />}
              action={null}
            >
              <PaletteWrapper>
                <PortalThemeList
                  themes={this.props.themes}
                  currentTheme={this.props.theme}
                  onChange={this._changeTheme}
                />
              </PaletteWrapper>
            </DevConsoleChildListItem>
            <DevConsoleChildListItem
              icon={<ColorizeIconWrapper />}
              content={<FormattedMessage {...messages.changePalette} />}
              action={null}
            >
              <PaletteWrapper>
                <CbPluginSettingEditor
                  type={SettingTypes.COLOR_TYPE}
                  name="Primary"
                  display_name="Primary"
                  value={this.props.theme.palette.defaultPrimary}
                  onChange={this._handlePrimaryColorChange}
                  size={12}
                />
                <CbPluginSettingEditor
                  type={SettingTypes.COLOR_TYPE}
                  name="Primary Text"
                  display_name="Primary Text"
                  value={this.props.theme.palette.primaryText}
                  onChange={this._handlePaletteTextChange.bind(
                    this,
                    'primaryText',
                  )}
                  size={12}
                />
                <CbPluginSettingEditor
                  type={SettingTypes.COLOR_TYPE}
                  name="Secondary"
                  display_name="Secondary"
                  value={this.props.theme.palette.defaultSecondary}
                  onChange={this._handleSecondaryColorChange}
                  size={12}
                />
                <CbPluginSettingEditor
                  type={SettingTypes.COLOR_TYPE}
                  name="Secondary Text"
                  display_name="Secondary Text"
                  value={this.props.theme.palette.secondaryText}
                  onChange={this._handlePaletteTextChange.bind(
                    this,
                    'secondaryText',
                  )}
                  size={12}
                />
              </PaletteWrapper>
            </DevConsoleChildListItem>

            <DevConsoleChildListItem
              icon={<TuneIconWrapper />}
              content={<FormattedMessage {...messages.adjustGraphColors} />}
              action={null}
            >
              <PaletteWrapper>
                {this._renderGraphColors(this.props.theme.graph)}
              </PaletteWrapper>
            </DevConsoleChildListItem>
          </div>
        )}

        {this.props.showAdvanced && (
          <CbStyleNavigator
            selectedMenus={this.props.selectedSubMenus}
            getFieldValue={this._getFieldValue}
            handleFieldChange={this._handleFieldChange}
            goBackToMenu={this.props.goBackToMenu}
            selectSubMenu={this.props.selectSubMenu}
          />
        )}
      </Wrapper>
    );
  }

  getThemeLevel = () =>
    this.props.showAdvanced ? microThemeValue : macroThemeValue;

  _getFieldValue = (fieldName: string) => {
    return get(this.props.theme.items, [
      ...this.props.currentMenuPath,
      fieldName,
    ]);
  };

  _handlePrimaryColorChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.props.handlePrimaryColorChange(e.target.value);
  };

  _handleSecondaryColorChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.props.handleSecondaryColorChange(e.target.value);
  };

  _handlePaletteTextChange = (
    field: string,
    e: React.ChangeEvent<HTMLInputElement>,
  ) => {
    this.props.handlePaletteTextChange(field, e.target.value);
  };

  _handleFieldChange = (
    field: string,
    e: React.ChangeEvent<HTMLInputElement>,
  ) => {
    this.props.handleFieldChange(
      this.props.currentMenuPath,
      field,
      e.target.value,
    );
  };

  _handleGraphColorChange = (
    field: string,
    e: React.ChangeEvent<HTMLInputElement>,
  ) => {
    this.props.handleGraphColorChange(field, e.target.value);
  };

  _toggleAccordion = (field: string) => {
    this.props.toggleAccordion(field);
  };

  _renderGraphColors = (graphColors: PortalMacroTheme['graph']) => {
    const rtn = [];
    for (const key in graphColors) {
      if (graphColors.hasOwnProperty(key)) {
        rtn.push(
          <CbPluginSettingEditor
            key={key}
            type={SettingTypes.COLOR_TYPE}
            name=""
            display_name=""
            value={graphColors[key as keyof PortalMacroTheme['graph']]}
            onChange={this._handleGraphColorChange.bind(this, key)}
          />,
        );
      }
    }
    return rtn;
  };

  _changeTheme = (idx: number) => {
    this.props.changeTheme(idx);
  };

  _toggleAdvanced = (e: { target: { value: any } }) => {
    if (e.target.value !== this.getThemeLevel()) {
      this.props.toggleAdvanced();
    }
  };
}

export default CbThemeEditor;



