/*
 *
 * CbThemeEditorContainer
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';

import injectReducer from '../../utils/injectReducer';

import {
  selectSelectedSubMenus,
  selectShowAdvanced,
  selectIsLoaded,
  selectCurrentMenuPath,
  selectIsPaletteEditorOpen,
  selectIsGraphColorEditorOpen,
  selectIsDropdownOpen,
} from './selectors';
import { selectTheme } from '../Portal/selectors';
import {
  goBackToMenu,
  selectSubMenu,
  toggleAdvanced,
  initSelectedSubMenu,
  toggleAccordion,
  toggleDropdown,
  changeThemeRequest,
  handleFieldChangeRequest,
  handlePrimaryColorChangeRequest,
  handleSecondaryColorChangeRequest,
  handlePaletteTextChangeRequest,
  handleGraphColorChangeRequest,
  CollapsibleElements,
} from './actions';

import getThemeConfig from './themeConfig';
import CbThemeEditor from '../../components/CbThemeEditor';
import LoadingIndicator from '../../components/LoadingIndicator';
import standardTheme from '../Portal/themes/standard';
import nightTheme from '../Portal/themes/night';
import createTheme from '../Portal/themes/createTheme';
import { STORE_KEY } from './constants';
import reducer, { ThemeMenuOptions } from './reducer';
import { CBDispatch } from 'cb-utils/typeUtils';
import { createStructuredSelector } from 'reselect';
const themes = [createTheme(standardTheme), createTheme(nightTheme)];

interface CbThemeEditorContainerProps {
  importThemeRequest: (e: React.ChangeEvent<HTMLInputElement>) => void;
}

export class CbThemeEditorContainer extends React.PureComponent<
  CbThemeEditorContainerProps & StateToProps & DispatchToProps
> {
  componentWillMount() {
    this.props.init();
  }

  render() {
    if (this.props.isLoaded) {
      return (
        <CbThemeEditor
          config={getThemeConfig()}
          themes={themes}
          {...this.props}
        />
      );
    } else {
      return <LoadingIndicator />;
    }
  }
}

const mapStateToProps = createStructuredSelector({
  isLoaded: selectIsLoaded(),
  selectedSubMenus: selectSelectedSubMenus(),
  showAdvanced: selectShowAdvanced(),
  theme: selectTheme(),
  currentMenuPath: selectCurrentMenuPath(),
  isPaletteEditorOpen: selectIsPaletteEditorOpen(),
  isGraphColorEditorOpen: selectIsGraphColorEditorOpen(),
  isDropdownOpen: selectIsDropdownOpen(),
});

function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    init: () => {
      dispatch(initSelectedSubMenu(getThemeConfig().menus));
    },
    goBackToMenu: (idx: number) => {
      dispatch(goBackToMenu(idx));
    },
    selectSubMenu: (menu: ThemeMenuOptions, idx: number) => {
      dispatch(selectSubMenu(menu, idx));
    },
    toggleAdvanced: () => {
      dispatch(toggleAdvanced());
    },
    handleFieldChange: (path: string[], field: string, value: any) => {
      dispatch(handleFieldChangeRequest(path, field, value));
    },
    changeTheme: (idx: number) => {
      dispatch(changeThemeRequest(themes[idx]));
    },
    handlePrimaryColorChange: (value: string) => {
      dispatch(handlePrimaryColorChangeRequest(value));
    },
    handleSecondaryColorChange: (value: string) => {
      dispatch(handleSecondaryColorChangeRequest(value));
    },
    handlePaletteTextChange: (field: string, value: string) => {
      dispatch(handlePaletteTextChangeRequest(field, value));
    },
    handleGraphColorChange: (field: string, value: string) => {
      dispatch(handleGraphColorChangeRequest(field, value));
    },
    toggleAccordion: (field: CollapsibleElements) => {
      dispatch(toggleAccordion(field));
    },
    toggleDropdown: () => {
      dispatch(toggleDropdown());
    },
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;
type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });

export default compose(withReducer, withConnect)(CbThemeEditorContainer);



